import axios from "axios";
import { baseURL } from "../AppConstants";

export const postData = async (endPt, data) => {
  try {
    let res = await axios.post(`${baseURL}${endPt}`, data);
    return res.data;
  } catch (err) {
    return err.response.data;
  }
};

export const getData = async endPt => {
  try {
    let res = await axios.get(`${baseURL}${endPt}`);
    return res.data;
  } catch (err) {
    return err.response.data;
  }
};
