<template>
  <footer class="footer-area section-gap">
    <div class="container">
      <div class="row">
        <div class="col-lg-5 col-md-6 col-sm-6">
          <div class="single-footer-widget">
            <h4>About Me</h4>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Totam eum quis fugit dolorum voluptas minus distinctio perspiciatis ex sequi quod!</p>
          </div>
        </div>
        <div class="col-lg-5 col-md-6 col-sm-6">
          <div class="single-footer-widget">
            <h4>Newsletter</h4>
            <p>Stay updated with our latest trends</p>
            <div class id="mc_embed_signup">
              <form @submit.prevent="submitEmail()">
                <div
                  v-if="message"
                  :class="success ? 'alert alert-success' : 'alert alert-danger'"
                >{{ message }}</div>
                <div class="input-group">
                  <input
                    v-model="email"
                    type="email"
                    class="form-control"
                    name="EMAIL"
                    placeholder="Enter Email Address"
                    onfocus="this.placeholder = ''"
                    onblur="this.placeholder = 'Enter Email Address '"
                  />
                  <div class="input-group-btn">
                    <button class="btn btn-default" type="submit">
                      <span class="lnr lnr-arrow-right"></span>
                    </button>
                  </div>
                  <div class="info"></div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="col-lg-2 col-md-6 col-sm-6 social-widget">
          <div class="single-footer-widget">
            <h4>Follow Me</h4>
            <p>Let us be social</p>
            <div class="footer-social d-flex align-items-center">
              <a target="_blank" href="https://www.facebook.com/mauryanarendra11">
                <i class="fa fa-facebook"></i>
              </a>
              <a target="_blank" href="https://twitter.com/___Narendra____">
                <i class="fa fa-twitter"></i>
              </a>
              <a target="_blank" href="https://www.youtube.com/channel/UC7fpWskUFi90sANs9QXRVsg">
                <i class="fa fa-youtube"></i>
              </a>
              <a target="_blank" href="https://www.github.com/nandymandy1">
                <i class="fa fa-github"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
import { postData } from "@/services/api";

export default {
  data() {
    return {
      email: "",
      message: null,
      success: null
    };
  },
  methods: {
    async submitEmail() {
      if (this.email !== "") {
        let res = await postData("services/newsletter", { email: this.email });
        if (res.success) {
          this.email = "";
          this.success = true;
        } else {
          this.success = false;
        }
        this.message = res.message;
      } else {
        this.message = "Email Address is required.";
        this.success = false;
      }
      setTimeout(() => {
        this.initState();
      }, 4000);
    },
    initState() {
      this.message = null;
      this.success = null;
    }
  }
};
</script>

<style scoped>
.alert {
  position: absolute;
  margin-top: 3rem;
}

.alert-success {
  background: green;
  color: #fff;
  border-radius: 0;
  border-color: green;
}

.alert-danger {
  background: red;
  color: #fff;
  border-radius: 0;
  border-color: red;
}
</style>
